<template>
    <div class="logistics">
        <p class="logistics-title">货运要求路线</p>
        <Card>
            <div>
                <p>已匹配到的物流路线:</p>
                <div class="logistics-line" v-for="(logisticsRoute,id) in logisticsRouteList" :key="id" @click="clickLine(id, logisticsRoute)" :class="{'logistics-click': num == id}">
                    <div class="logistics-btn" v-for="(logistics, index) in logisticsRoute.location" :key="index">
                        <span>{{logistics.city}}</span>
                        <span>——</span>
                    </div>
                </div>
            </div>
            <div>
                <p>未匹配到的物流路线:</p>
                <div class="logistics-line" v-for="(logisticsRoute,id) in logisticsRouteList" :key="id">
                    <div class="logistics-btn" style="cursor: not-allowed; color: #999999" v-for="(logistics, index) in logisticsRoute.location" :key="index">
                        <span>{{logistics.city}}</span>
                        <span>——</span>
                    </div>
                </div>
            </div>
        </Card>
    </div>
</template>

<script>
export default {
    name: 'AdtLogisticsRoute',
    props: {
        logisticsRouteList: {
            type: Array,
            default: function () {
                return []
            }
        }
    },
    data() {
        return {
            // 控制点击选择路线
            num: '默认值'
        }
    },
    methods: {
        clickLine (id, val) {
            this.num = id
            this.$emit("clickPath", val)
        }
    },
}
</script>

<style lang="scss" scoped>
.logistics{
    .logistics-title{
        font-size: $font-size-large;
        margin: 20px 0 10px 0;
    }
    /deep/.ivu-card-body{
        padding: 30px 16px;
        p{
            font-size: $font-size-base;
            color: $conventional-font-color;
            font-weight: bold;
        }
        .logistics-line{
            padding: 10px 40px;
            margin: 10px 20px 30px 0;
            display: inline-block;
            font-size: $font-size-small;
            background-color: $filling-color;
            font-weight: bold;
            border: 1px solid $filling-color;
            border-radius:4px;
            .logistics-btn{
                display: inline-block;
                &:last-child{
                    span{
                        &:last-child{
                            display: none;
                        }
                    }
                }
            }
        }
        .logistics-click{
            background-color:$info-prompt-color;
            border: 1px solid $light-color;
        }
    }
}
</style>